.load-file-page{
    display:flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
} 

.load-file-page>label{
    width: 120px;
    height: 80px;
    line-height: 50px;
    text-align: center;
    border: solid 2px darkgray;
    border-radius: 15px;  
    display: flex;  
    justify-content: center;
    align-items: center;
} 

.load-file-page>label>input{
    display: none;
} 

.splayer-page{
    padding: 10px;
}

.splayer-page__toolbar{
    margin-bottom: 10px;
    display: flex;
    flex-wrap: wrap;
    row-gap: 10px;
}

.toolbar-button{
    width: 64px;
    height: 64px;
    background-color: blanchedalmond;
    border-radius: 10px;
    margin-right: 10px;
}

.toolbar-button__enabled{
    background-color: #d3ffd3;
}

.toolbar-button__disabled{
    background-color: #f5f5f5;
    color: lightgray;
    border-color: lightgray;
}

.toolbar-button__text{
    font-weight: 600;
    font-size: 20px;
}

.toolbar-button>div{
    width: 54px;
    height: 54px;
    margin-left: -3px;
}

.splayer_page__text{
    font-size: 30px;
}

.pause-tuner{
    background-color: blanchedalmond;
    border-left: 2px red;
    border-radius: 10px;
    width: 48px;
    height: 60px; 
    line-height: 60px;
    text-align: center;
    margin-right: 10px;
    border-left: solid 8px red;
    border-right:solid 8px red;
    border-top: solid 2px lightgray;
    border-bottom: solid 2px lightgray;
}
 
.pause-tuner>#label{
    position: relative;
    top: -20px;
} 
.pause-tuner>#value{
    position: relative;
    top: -55px;
    font-weight: 700;
} 



.img32Icondiv{
